svg-button,\n  .svg-link,\n  .link {\n    &:hover {\n      box-shadow: 0 0 0.3em 0.3em $lightBlue !important;\n    }\n  }\n  .link:hover {color: $darkRed;}\n}\n\n.button,\n.input,\n.textarea,\n.link,\n.svg-button,\n.svg-link {\n  transition: $focusTransition;\n\n  &:focus {box-shadow: $focusShadow;}\n}\n\n.link {\n  text-decoration: none;\n  color: inherit;\n\n  &:focus {\n    text-decoration: underline;\n    color: $mediumRed;\n  }\n\n  &--active-tab {font-weight: 600;}\n}\n\n.svg {\n  &-icon,\n  &-button,\n  &-link {\n    height: 24px;\n    width: 24px;\n    border-radius: 3px;\n  }\n\n  &-button,\n  &-link {\n    display: block;\n  }\n\n  &-button {\n    stroke: $mediumBlack;\n    background-color: unset;\n  }\n\n  &--fill:focus {fill: $mediumRed;}\n  &--stroke:focus {stroke: $mediumRed;}\n}\n\n.input,\n.textarea {\n  padding: 0.2em 0.5em;\n  border-radius: 0.4em;\n}\n.textarea {\n  min-width: 100%;\n  max-width: 100%;\n}\n","$greyTestimonialsTitle: #667085;\n$greyImgBorder: #DEDCDB;\n\n$greyBack: #F6F6F6;\n$whiteBack: #FCFCFC;\n\n$mediumBlack: #272D38;\n\n$lightBlue: #CFDCED;\n$mediumBlue: #8EADD5;\n$darkBlue: #5A87C2;\n\n$mediumGreen: #2CAF8A;\n\n$mediumRed: #D34F64;\n$darkRed: #BA2E45;\n\n$lightYellow: #F3DEC2;\n$mediumYellow: #D8963F;\n$darkYellow: #BD7528;\n","@use 'colors' as *;\n\n$focusTransition: box-shadow 0.25s ease-in-out;\n$focusShadow:  0 0 0.6em 0.2em $mediumYellow;\n","@use 'mixins' as *;\n@use 'colors' as *;\n\n.display--none {display: none !important;}\n\n\n@media screen and (pointer: fine) {\n  .button--common:hover {background-color: $darkBlue;}\n}\n.button--common {\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  padding: 0.7em 0.5em;\n  border-radius: 0.6em;\n  text-transform: uppercase;\n  color: #FFFFFF;\n  background-color: $mediumBlue;\n  @include lineFontParams(172%, 0.8em, 600);\n\n  &:focus {background-color: $mediumGreen;}\n}\n","@mixin wrapperStyle($width) {\n  max-width: $width;\n  width: 100%;\n  margin-right: auto;\n  margin-left: auto;\n}\n\n@mixin lineFontParams($lineHeight, $fSize, $fWeight) {\n  line-height: $lineHeight;\n  font-size: $fSize;\n  font-weight: $fWeight;\n}\n","@use './../mixins' as *;\n@use './../colors' as *;\n\nbody {overflow: hidden auto;}\n\n.body-container {\n  font-size: 20px;\n  font-family: 'Inter', sans-serif;\n  font-style: normal;\n  color: $mediumBlack;\n  background-color: $whiteBack;\n\n  &__header-container {\n    .container-wrapper {@include wrapperStyle(54.3em);}\n  }\n  &__main-container {\n    margin-top: 8.53em;\n    .container-wrapper {@include wrapperStyle(53.3em);}\n  }\n  &__footer-container {\n    margin-top: 6.9em;\n    .container-wrapper {@include wrapperStyle(53.7em);}\n  }\n}\n\n@media screen and (max-width: 1150px) {\n  .body-container {\n    &__header-container,\n    &__main-container,\n    &__modal-container {\n      .container-wrapper {\n        padding-right: 1.4em;\n        padding-left: 1.4em;\n        font-size: 17.5px;\n      }\n    }\n    &__main-container {margin-top: 6.63em;}\n    &__footer-container {margin-top: 6.2em;}\n  }\n}\n\n@media screen and (max-width: 400px) {\n  .body-container {\n    &__header-container,\n    &__footer-container {\n      .container-wrapper {font-size: 16px;}\n    }\n\n    &__main-container,\n    &__modal-container {\n      .container-wrapper {font-size: 14px;}\n    }\n  }\n}\n","@use './../mixins' as *;\n@use './../colors' as *;\n\n$commonHeaderTop: 2.3em;\n\n.header-container {\n  position: fixed;\n  top: 0;\n  width: 100%;\n  transition: all .3s linear;\n  z-index: 50;\n\n  .container-wrapper {\n    display: flex;\n    justify-content: space-between;\n    align-items: center;\n    flex-wrap: wrap;\n  }\n\n  &__logo-link {\n    margin-left: 0.5em;\n    text-transform: uppercase;\n    @include lineFontParams(120%, 1em, 700);\n  }\n  &__nav-container {text-transform: lowercase;}\n\n  &--menu-opened {\n    z-index: 10;\n    position: fixed;\n    top: 0;\n    height: 100%;\n    width: 100%;\n    padding-top: $commonHeaderTop;\n    background: linear-gradient(155deg, $lightBlue, white, white, $lightYellow);\n    overflow-y: auto;\n\n    .nav-container {\n      width: 100%;\n      margin-top: 2.5em;\n      margin-bottom: 2.5em;\n    }\n  }\n\n  &--normal {\n    padding-top: $commonHeaderTop;\n    background-color: transparent;\n  }\n  &--lesser {\n    padding-top: .4em;\n    padding-bottom: .4em;\n    background-color: rgba(207, 220, 237, 0.95); // $lightBlue\n  }\n}\n\n.nav-container {\n  &__ul-list {\n    display: flex;\n    justify-content: space-between;\n    flex-wrap: wrap;\n    gap: 2.55em 3em;\n\n    .li-item {@include lineFontParams(153%, 0.9em, 400);}\n  }\n}\n\n@media screen and (max-width: 700px) {\n  .header-container{\n    &__logo-link {\n    margin-left: unset;\n    text-transform: capitalize;\n    font-size: 0.8em;\n    }\n  }\n\n  .nav-container {\n    &__ul-list {\n      flex-direction: column;\n      align-items: center;\n    }\n  }\n}\n","@use './../mixins' as *;\n@use './../colors' as *;\n\n.footer-container {\n  padding-top: 5.35em;\n  padding-bottom: 0.9em;\n  text-transform: capitalize;\n  background-color: $greyBack;\n\n  &__copywrite-container {\n    margin-top: 5.5em;\n    text-align: center;\n    text-transform: initial;\n  }\n}\n\n.copywrite-container__p-item,\n.links-container .li-item {\n  @include lineFontParams(100%, 0.8em, 300);\n}\n\n.about-container,\n.links-container {\n  .title {@include lineFontParams(153%, 0.9em, 700);}\n}\n\n.links-container {\n  display: grid;\n  grid-template-columns: 10em repeat(3, auto);\n  grid-gap: 3.5em 8.5em;\n\n  .ul-list {\n    margin-top: 1.6em;\n\n    .li-item {\n      margin-bottom: 1.5em;\n      &:last-of-type {margin-bottom: unset;}\n    }\n  }\n}\n\n.about-container {\n  &__p-item {\n    max-width: 12em;\n    margin-top: 2.45em;\n    @include lineFontParams(150%, 0.8em, 300);\n  }\n}\n\n@media screen and (max-width: 1150px) {\n  .about-container {grid-area: 1/1/2/4;}\n  .